import React from 'react';
import { CardContainer, CardContainerProps, ProjectInfoCard } from './components';

export type PrintableProps = CardContainerProps & {
  title?: string;
};

export const Printable = (props: PrintableProps) => {
  return (
    <CardContainer style={props.style} name={props.name} url={props.url} >
      <ProjectInfoCard title={props.title} />
    </CardContainer>
  );
};

export default Printable;
